      <div class="section-heading">
        <h1>Testimonials.</h1>
        <span>Testimonials</span>
      </div>
      <div
        id="carouselExampleDark"
        class="carousel carousel-light slide testimonials-text"
        data-bs-ride="carousel"
      >
        <div class="carousel-indicators">
          <button
            type="button"
            data-bs-target="#carouselExampleDark"
            data-bs-slide-to="0"
            class="active"
            aria-label="Slide 1"
            aria-current="true"
          ></button>
          <button
            type="button"
            data-bs-target="#carouselExampleDark"
            data-bs-slide-to="1"
            aria-label="Slide 2"
            class=""
          ></button>
          <button
            type="button"
            data-bs-target="#carouselExampleDark"
            data-bs-slide-to="2"
            aria-label="Slide 3"
            class=""
          ></button>
          <button
            type="button"
            data-bs-target="#carouselExampleDark"
            data-bs-slide-to="3"
            aria-label="Slide 4"
            class=""
          ></button>
          <button
            type="button"
            data-bs-target="#carouselExampleDark"
            data-bs-slide-to="4"
            aria-label="Slide 5"
          ></button>
        </div>
        <div class="carousel-inner">
          <div
            v-for="testimonial in testimonials"
            :key="testimonial"
            class="carousel-item carousel-item-next carousel-item-start"
            data-bs-interval="1000"
          >
            <div class="row justify-content-center align-items-center">
              <div class="col-md-5 text-center text-md-start color">
                <h1>
                  <div class="display-6">{{ testimonial.name }}</div>
                </h1>
                <h3>{{ testimonial.attestant }}</h3>
                <p class="lead my-4">
                  {{ testimonial.comment }}
                </p>
              </div>

              <div class="col-md-5 text-center d-none d-md-block">
                <img
                  :src="testimonial.image"
                  class="img-rounded"
                  alt="Cinque Terre"
                />
              </div>
            </div>
          </div>

          <button
            class="carousel-control-prev"
            type="button"
            data-bs-target="#carouselExampleDark"
            data-bs-slide="prev"
          >
            <span class="carousel-control-prev-icon" aria-hidden="true"></span>
            <span class="visually-hidden">Previous</span>
          </button>
          <button
            class="carousel-control-next"
            type="button"
            data-bs-target="#carouselExampleDark"
            data-bs-slide="next"
          >
            <span class="carousel-control-next-icon" aria-hidden="true"></span>
            <span class="visually-hidden">Next</span>
          </button>
        </div>
      </div>
      <hr class="dashed" />
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      testimonials: [],
    };
  },
  mounted() {
    fetch("https://vuelilitha.herokuapp.com/testimonials ")
      .then((res) => res.json())
      .then((data) => {
        this.testimonials = data;
        console.log(data, this.testimonials);
      });
  },
};
</script>

